*{margin:0;padding:0;line-height:normal;font-weight:400}.header{background-color:#444;color:rgba(255,255,255,0.7);padding-top:13px}.header__logo{width:100%;max-width:164px}.header__search-input{margin-bottom:0;background-color:#f2f2f2;border:none}.header__search-input:focus{background-color:#fff;border:none}.header__git{text-align:right}.header__git-link{color:rgba(255,255,255,0.7)}.header__git-link:hover,.header__git-link:active{color:#fff}.header-nav__list{display:flex;list-style:none}.pl-button{padding:18px;margin-bottom:0}.pl-button.link{color:rgba(255,255,255,0.7)}.pl-button.link:hover{color:#fff}.img-wrapper{overflow:hidden;border-radius:.25rem;position:relative}.films-wrapper{margin-top:30px;text-align:center;max-height:550px;height:100%}.films-wrapper_icon{max-width:100%;height:auto;border-radius:.25rem;transition:0.3s}.films-wrapper_icon:hover{transform:scale(1.1)}.films-wrapper_vote{background-image:url("../img/star.png");position:absolute;right:3px;top:3px;background-size:cover;width:47px;height:47px;padding-top:15px;padding-left:1px;font-weight:bold}.films-wrapper_desc{height:94px;font-weight:300;padding:0px 5px;overflow:hidden}.details-overlay__img{width:100%}@media (min-width: 768px){#sidebar-container{position:sticky;display:block}}
